<template>
    <div v-loading="loading">
        <el-form ref="form" :model="form" label-width="180px">
            <el-row class="toolbar bgc-fff">
                <el-col :span="12">
                    <h2 class="fs-18">当前订单状态：<span  class="color-red">待提交</span></h2>
                    <p class="fs-14">当前处理人：admin</p>
                </el-col>
                <!-- <el-col :span="12" class="ta-r">
                    <el-button type="primary" icon="plus">导出</el-button>
                    <el-button type="primary" icon="plus">打印</el-button>
                </el-col> -->
            </el-row>
            <el-collapse v-model="collapseActive" @change="">
                <el-collapse-item title="　基本信息" name="1">
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="车牌号">
                                {{form.base_info.plate_number}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="vin号">
                                {{form.base_info.vin_number}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="车辆品牌">
                                {{form.base_info.car_brand}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="马力">
                                {{form.base_info.horse_power}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="公告型号">
                                {{form.base_info.foreign_model}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="驱动形式">
                                {{form.base_info.drive_form}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="颜色">
                                {{form.base_info.color}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="业务发起车主">
                                {{form.base_info.band_name}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="过户车主">
                                <el-button @click="chooseOwner" :disabled="isDsiabledBase()">{{form.transfer.name}}</el-button>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="业务发起车主移动电话">
                                {{form.base_info.band_mobile}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="过户车主移动电话">
                                {{form.transfer.mobile}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="业务发起车主身份证号">
                                {{form.base_info.id_card}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="过户车主身份证号">
                                {{form.transfer.id_card}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <!-- <el-row>
                        <el-col :span="12">
                            <el-form-item label="业务发起车主挂靠公司">
                                {{form.base_info.bus_mech_name}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="过户车主挂靠公司">
                                {{form.transfer.partnership_company}}
                            </el-form-item>
                        </el-col>
                    </el-row> -->

                </el-collapse-item>

                <el-collapse-item title="　备注信息" name="2">

                    <el-input type="textarea" v-model="form.car_remark" placeholder="请输入备注信息" :disabled="isDsiabledBase()"></el-input>

                    <el-form-item label="选择审核人" class="mt15" v-if="isShowFirstAudit()">
                        <el-select v-model="form.cp_id" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                            <el-option
                                v-for="item in optionsCompany"
                                :key="item.id"
                                :label="item.mc_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                        <el-select v-model="form.after_user_id" placeholder="请选择审核人">
                            <el-option
                                v-for="item in optionsExecUser"
                                :key="item.id"
                                :label="item.user_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>

                </el-collapse-item>

                <el-collapse-item title="　收款信息" name="3" v-if="isShowReceipt()">

                    <el-form-item label="车主支付方式">
                        <el-radio-group v-model="form.some_collect_cast.pay_method" :disabled="isDsiabledReceipt()">
                            <el-radio label="1">现付</el-radio>
                            <el-radio label="2">转账</el-radio>
                        </el-radio-group>
                    </el-form-item>

                    <el-form-item label="欠款金额">
                        {{form.some_collect_cast.non_payment}}
                    </el-form-item>

                    <el-form-item label="费用凭证">
                        <el-upload
                            :disabled="isDsiabledReceipt()"
                            class="avatar-uploader"
                            :action="IMG_UPLOAD_URL"
                            :show-file-list="false"
                            :on-success="handleUploadSuccess_cost_img"
                            :before-upload="handleBeforeUpload">
                            <img v-if="form.some_collect_cast.cost_img" :src="form.some_collect_cast.cost_img" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>
                    
                    <el-form-item label="备注">
                        <el-input type="textarea" v-model="form.some_collect_cast.refund_remark" :disabled="isDsiabledReceipt()"></el-input>
                    </el-form-item>

                </el-collapse-item>

                <el-collapse-item title="　办理信息" name="4" v-if="form.node_id>=245">

                    <el-form-item label="买卖协议">
                        <el-upload
                            :disabled="isDisabledPerfect()"
                            :action="IMG_UPLOAD_URL"
                            :on-success="handleUploadSuccess_business_img"
                            :file-list="form.business_img"
                            list-type="picture-card">
                            <i class="el-icon-plus"></i>
                        </el-upload>
                    </el-form-item>

                    <el-form-item label="挂靠协议">
                        <el-upload
                            :disabled="isDisabledPerfect()"
                            :action="IMG_UPLOAD_URL"
                            :on-success="handleUploadSuccess_lean_img"
                            :file-list="form.lean_img"
                            list-type="picture-card">
                            <i class="el-icon-plus"></i>
                        </el-upload>
                    </el-form-item>
                    
                    <el-form-item label="安全协议">
                        <el-upload
                            :disabled="isDisabledPerfect()"
                            :action="IMG_UPLOAD_URL"
                            :on-success="handleUploadSuccess_safe_img"
                            :file-list="form.safe_img"
                            list-type="picture-card">
                            <i class="el-icon-plus"></i>
                        </el-upload>
                    </el-form-item>

                </el-collapse-item>

                <el-collapse-item title="　审核信息" name="5" v-if="isShowAudit()">
                    
                    <el-collapse v-model="collapseAuditActive">

                        <el-collapse-item title="　收款审核" name="1">
                            <el-card class="box-card mb15" v-for="(item,index) in form.audit_info.pay" :key="index">
                                <div><span class="display-ib ta-r w70">订单流程：</span>{{item.node_name}}</div>
                                <div><span class="display-ib ta-r w70">审核意见：</span>{{item.exec_op_type==1?'通过':'驳回'}}</div>
                                <div><span class="display-ib ta-r w70">备注：</span>{{item.exec_content}}</div>
                                <div><span class="display-ib ta-r w70">审核人ID：</span>{{item.exec_user_name}}</div>
                                <div><span class="display-ib ta-r w70">审核时间：</span>{{item.create_time}}</div>
                            </el-card>
                        </el-collapse-item>

                        <template v-if="form.node_id==203">
                            <el-form-item label="是否欠款">
                                <el-radio-group v-model="form.is_arr">
                                    <el-radio label="1">是</el-radio>
                                    <el-radio label="2">否</el-radio>
                                </el-radio-group>
                            </el-form-item>
                            <template v-if="isShowDebt()">
                                <el-form-item label="业务欠款">
                                    <el-collapse v-model="collapseActiveIsDebt" accordion>
                                        <el-checkbox-group v-model="form.arr_ids">
                                            <el-collapse-item :title="`${form.bus_info.plate_number} ￥${form.arr_info.total_arr_money}`" name="1">
                                                <el-checkbox
                                                    v-for="item in form.arr_info.all_arr"
                                                    :key="item.id"
                                                    :label="item.id">
                                                    {{item.type}} {{item.arr_money}}
                                                </el-checkbox>
                                            </el-collapse-item>
                                            <!-- <el-collapse-item :title="`其他车辆 ￥${form.per_no_pay_total}`" name="2">
                                                <el-checkbox
                                                    v-for="item in form.per_no_pay"
                                                    :key="item.id"
                                                    :label="item.id">
                                                    {{item.item_name}} {{item.money}}
                                                </el-checkbox>
                                            </el-collapse-item> -->
                                        </el-checkbox-group>
                                    </el-collapse>
                                </el-form-item>
                                <el-form-item label="其他欠款">
                                    <el-input v-model="form.other_arr_money"></el-input>
                                </el-form-item>
                                <el-form-item label="需支付欠款总金额">
                                    <el-input v-model="form.all_arr_money"></el-input>
                                </el-form-item>
                            </template>

                            <el-form-item label="备注信息">
                                <el-input type="textarea" v-model="form.other_arr_remark"></el-input>
                            </el-form-item>
                        </template>
                        

                        <!-- <el-form-item label="处理凭证">
                            <el-upload
                                :action="IMG_UPLOAD_URL"
                                :on-success="handleUploadSuccess_handle_img"
                                :file-list="form.handle_img"
                                list-type="picture-card">
                                <i class="el-icon-plus"></i>
                            </el-upload>
                        </el-form-item>
                        
                        <el-form-item label="处理备注">
                            <el-input type="textarea" v-model="form.remark"></el-input>
                        </el-form-item> -->

                        <template v-if="isShowOpinion()">
                            <el-form-item label="审核意见" class="mt15">
                                <el-select v-model="form.opinion" placeholder="请选择" class="mr10">
                                    <el-option label="通过" value="1"></el-option>
                                    <el-option label="驳回" value="2"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="备注">
                                <el-input type="textarea" v-model="form.remark"></el-input>
                            </el-form-item>
                            <el-form-item label="选择审核人" class="mt15" v-if="form.opinion==1">
                                <el-select v-model="form.cp_id" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                                    <el-option
                                        v-for="item in optionsCompany"
                                        :key="item.id"
                                        :label="item.mc_name"
                                        :value="item.id">
                                    </el-option>
                                </el-select>
                                <el-select v-model="form.after_user_id" placeholder="请选择审核人">
                                    <el-option
                                        v-for="item in optionsExecUser"
                                        :key="item.id"
                                        :label="item.user_name"
                                        :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </template>

                        <el-form-item label="选择审核人" class="mt15" v-if="isShowLastAudit()">
                            <el-select v-model="form.cp_id" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                                <el-option
                                    v-for="item in optionsCompany"
                                    :key="item.id"
                                    :label="item.mc_name"
                                    :value="item.id">
                                </el-option>
                            </el-select>
                            <el-select v-model="form.after_user_id" placeholder="请选择审核人">
                                <el-option
                                    v-for="item in optionsExecUser"
                                    :key="item.id"
                                    :label="item.user_name"
                                    :value="item.id">
                                </el-option>
                            </el-select>
                        </el-form-item>

                    </el-collapse>
                </el-collapse-item>

            </el-collapse>
            <el-form-item class="form-submit">
                <el-button type="primary" @click="handleSubmit(1)" v-loading="loading" v-if="pageType != 'view'">提 交</el-button>
                <!-- <el-button @click="dialogClose = true">关闭</el-button> -->
                <el-button type="primary" @click="handleSubmit(2)" v-loading="loading" v-if="isShowSave()">保 存</el-button>
                <el-button @click="$router.back()">返 回</el-button>
            </el-form-item>
        </el-form>

        <!-- 关闭 -->
        <el-dialog title="关闭" :visible.sync="dialogClose" width="50%">
            <el-form :model="filtersClose" label-width="50px">
                <el-tag type="danger" class="mb10 ml50">确认关闭该工单吗？如确认关闭请填写备注信息。</el-tag>
                <el-form-item label="备注">
                    <el-input type="textarea" v-model="filtersClose.msg"></el-input>
                </el-form-item>
            </el-form>
            
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogClose = false">取 消</el-button>
                <el-button type="primary" @click="">确 定</el-button>
            </span>
        </el-dialog>
        
        <!-- 选择过户车主 -->
        <el-dialog title="选择过户车主" :visible.sync="dialogSelectClient" width="50%">
            <selectClient v-if="hackResetClient" :selectClientOptions="selectClientOptions" @emitClientSubmit="emitClientSubmit" @emitClientClose="emitClientClose"></selectClient>
        </el-dialog>
        
    </div>
</template>

<script>
    import * as API from '@/api/trafficManagement'
    import { requestUrl } from '@/api/config'
    import selectClient from '@/components/selectClient'
    import { organizationListUl } from '@/api/enterpriseManagement'
    export default {
        data: function(){
            return {
                IMG_UPLOAD_URL: `${requestUrl}/home/UpLoadFile/uploadImg`,
                collapseActive: ['1'],
                collapseAuditActive: ['1'],
                collapseActiveIsDebt: ['1'],
                pageType: '',
                loading: false,
                hackResetClient: false,
                dialogClose: false,
                filtersClose: {
                    msg: ''
                },
                dialogSelectClient: false,
                selectClientOptions:{
                    pageType: 'people'
                },
                optionsExecUser:[],
                optionsCompany:[],
                form: 
{
    cp_id:'',
    opinion: '1',
    tran_type: "1",
    tran_id: "",
    tran_name: "",
    telphone: "",
    after_user_id: "",
    
    remark: "",
    car_remark: "",
    is_arr: "1",
    other_arr_remark: "",
    other_arr_money: "0",
    all_arr_money: "0",
    business_img: [],
    lean_img: [],
    safe_img: [],
    is_arr_ids: [],
    work_id: "0",
    node_id: "0",
    status: "1",
    
    transfer:{
        name: "请选择车主",
        mobile: "",
        id_card: "",
        bus_mech_name: "",
    },
    some_collect_cast: {
        pay_method: "1",
        non_payment: "",
        together_cost: "0",
        cost_img: "",
        refund_remark: "",
    },
    base_info: {
        vin_number: "",
        car_brand: "",
        car_series: "",
        car_class: "",
        foreign_model: "",
        fuel_type: "",
        horse_power: "",
        drive_form: "",
        color: "",
        band_id: "",
        band_name: "",
        bus_mech_name: "",
        plate_number: ""
    },
    arr_info: {
        all_arr:[
            {
                id: "29",
                item_id: "119",
                item_name: "运营证年检",
                money: "1123"
            },
            {
                id: "30",
                item_id: "120",
                item_name: "运营证年检",
                money: "12311"
            },
            {
                id: "31",
                item_id: "121",
                item_name: "运营证年检",
                money: "1231"
            },
            {
                id: "32",
                item_id: "122",
                item_name: "运营证年检",
                money: "12351"
            }
        ],
        total_arr_money: 80
    },
    no_pay_money_total: 4
}
                ,
            }
        },
        components:{
            selectClient
        },
        computed:{
            
        },
        mounted(){
            this.getFormData()
        },
        methods: {
            isDsiabledBase(){
                return this.form.node_id!=232 && this.form.node_id!=236 && this.form.node_id!=0
            },
            isDisabledPerfect(){
                return this.form.node_id>245
            },
            isDsiabledReceipt(){
                return this.form.node_id>237
            },
            isShowFirstAudit(){
                return this.form.node_id==232 || this.form.node_id==0
            },
            isShowReceipt(){
                return this.form.node_id>=237 && this.form.is_arr==1
            },
            isShowAudit(){
                return this.form.node_id!=232 && this.form.node_id!=0
            },
            isShowSave(){
                if(this.pageType!='view' && this.form.node_id!=171 && this.form.node_id!=160){
                    return this.form.work_status!=2
                }
            },
            isShowOpinion(){
                return this.form.work_status==2 && this.form.node_id!=245
            },
            isShowLastAudit(){
                if(this.form.stage_id==8){
                    return this.form.work_status==1 || this.form.work_status==3
                }
            },
            getFormData() {
                let _path = this.$route.path,
                    _query = this.$route.query;

                _path.indexOf('view') > -1 ? this.pageType='view' : 'edit'

                // 公司列表
                organizationListUl({}).then(res => {
                    if(res.code == 0){
                        this.optionsCompany = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })

                this.loading=true
                API.apiAnomalyCarChangeView({id: _query.id}).then(res => {
                    this.loading=false
                    if(res.code == 0){
                        Object.assign(this.form, res.data)
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            getExecUser(){
                // 审核人
                let type='',
                    get_type=''
                this.form.some_collect_cast.pay_method==1?type=1:type=2
                if(this.form.node_id==232 || this.form.node_id==239 || this.form.node_id==0){
                    get_type=3
                }else{
                    get_type=2
                }
                let params = {
                    cp_id:this.form.cp_id,
                    type:type,
                    get_type:get_type,
                    node_id:this.form.node_id,
                }
                this.form.after_user_id = ''
                API.apiAnomalyCarChangeGetExecUser(params).then(res => {
                    if(res.code == 0){
                        this.optionsExecUser = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            handleSelectAuditor(val){
                this.getExecUser();
            },
            handleSubmit(submitType) {
                if(this.form.node_id==232 || this.form.node_id==0){
                    let params = {
                        id: this.$route.query.id,
                        operation_uniqid:this.form.operation_uniqid,
                        submit_type: submitType,
                        after_user_id: this.form.after_user_id,
                        car_remark: this.form.car_remark,
                        transfer: this.form.transfer,
                    }
                    API.apiAnomalyCarPaySubmit(params).then(res => {
                        if(res.code == 0){
                            this.$message.success('提交成功！')
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }else if(this.form.node_id == 26){
                    let params = {
                        is_arr: this.form.is_arr,
                        is_arr_ids: this.form.is_arr_ids,
                        other_arr_remark: this.form.other_arr_remark,
                        other_arr_money: this.form.other_arr_money,
                        all_arr_money: this.form.all_arr_money,
                        submit_type: submitType,
                        after_user_id: this.form.after_user_id,
                        bus_id: this.form.bus_id,
                        operation_uniqid:this.form.operation_uniqid,
                        id: this.form.id
                    }
                    API.apiAnomalyCarRefundSubmit(params).then(res => {
                        if(res.code == 0){
                            this.$message.success('提交成功！')
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }else if(this.form.node_id == 2){
                    let params = {
                        is_arr: this.form.is_arr,
                        is_arr_ids: this.form.is_arr_ids,
                        other_arr_remark: this.form.other_arr_remark,
                        other_arr_money: this.form.other_arr_money,
                        all_arr_money: this.form.all_arr_money,
                        submit_type: submitType,
                        after_user_id: this.form.after_user_id,
                        bus_id: this.form.bus_id,
                        id: this.form.id
                    }
                    API.apiAnomalyCarHandleArr(params).then(res => {
                        if(res.code == 0){
                            this.$message.success('提交成功！')
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }else if(this.form.node_id == 7){
                    let params = {
                        business_img: this.form.business_img,
                        lean_img: this.form.lean_img,
                        safe_img: this.form.safe_img,
                        id: this.form.id
                    }
                    API.apiAnomalyCarAgreement(params).then(res => {
                        if(res.code == 0){
                            this.$message.success('提交成功！')
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }else{
                    let perfectParams = {
                        usiness_img: this.form.business_img,
                        lean_img: this.form.lean_img,
                        safe_img: this.form.safe_img,
                        perfect_info: 1
                    }
                    let params = {
                        id: this.form.id,
                        submit_type: submitType,
                        after_user_id: this.form.after_user_id,
                        remark: this.form.remark,
                        reject_type: this.form.node_id==234?'1':'2',
                        exam_type: this.form.opinion,
                        operation_uniqid:this.form.operation_uniqid,
                    }
                    if(this.form.node_id==245){
                        Object.assign(params, perfectParams)
                    }
                    API.apiAnomalyCarWorkExec(params).then(res => {
                        if(res.code == 0){
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }
            },
            handleUploadSuccess_cost_img(res, file, fileList){
                this.form.some_collect_cast.cost_img = res.data.url
            },
            handleUploadSuccess_business_img(res, file, fileList){
                this.form.business_img.push({
                    uid: file.uid,
                    url: res.data.url
                })
            },
            handleUploadSuccess_lean_img(res, file, fileList){
                this.form.lean_img.push({
                    uid: file.uid,
                    url: res.data.url
                })
            },
            handleUploadSuccess_safe_img(res, file, fileList){
                this.form.safe_img.push({
                    uid: file.uid,
                    url: res.data.url
                })
            },
            handleBeforeUpload(){

            },

            handleDelete(row, index) {
                
            },
            chooseOwner(){
                this.dialogSelectClient = true;
                this.hackResetClient = false
                this.$nextTick(() => {
                    this.hackResetClient = true
                })
            },
            emitClientClose(){
                this.dialogSelectClient = false;
            },
            emitClientSubmit(row){
                this.dialogSelectClient = false;
                Object.assign(this.form.transfer, row)
            }
        }
    }
</script>

